export default {
	name: "SearchBar",
	data() {
		return {
			searchVal: ""
		}
	},
	props: {
		placeholder: {
			type: String,
			default: "请输入搜索内容"
		},
	},
	watch: {
		searchVal() {
			this.$emit("input", {
				value: this.searchVal
			})
		}
	},
	methods: {
		confirm() {

			uni.hideKeyboard();




			this.$emit("confirm", {
				value: this.searchVal
			})
		}
	}
}
